import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import { GamePage } from '../game/game';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  scores: any;

  constructor(
    public navCtrl: NavController
  ) {}

  toGame():void{
    this.navCtrl.push(GamePage);
  }

}



