import { Component , ViewChild} from '@angular/core';
import { IonicPage, NavController, NavParams, Platform } from 'ionic-angular';

import { GameOverPage } from '../game-over/game-over';

export class Player{
  x:number;
  y:number;
  h:number;
  w:number;
  s:number; //speed
  c:string; //color
  d:string; //direction
}

export class Spawn{
  x:number;
  y:number;
  h:number;
  w:number;
  s:number; //speed
  c:string; //color
  t:string; //type (poision|bonus)
  p:number; //points (point value of a given spawn)
  counted:boolean; //has this spawn been counted towards the score
}

@IonicPage()
@Component({
  selector: 'page-game',
  templateUrl: 'game.html',
})
export class GamePage {

  @ViewChild('canvas') canvas;

  ctx:any;
  body:any;
  score:number=0;
  spawns:Spawn[];
  spawner:any;
  dropSpeed:number=400;
  player:Player;
  gamePlay:boolean=true;
  lastThousand:number=0;

  spawnSpeedMin:number=1;
  spawnSpeedMax:number=5;
  playerSpeed:number=3;
  baseWidth:number = 320;
  baseHeight:number = 568;
  sizeMultiplier:number=1;
  scoreMultiplier:number=1;


  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams, 
    //private device:Device,
    public platform: Platform
  ) {}

  init():void {
    this.canvas=document.querySelector('canvas');
    this.ctx=this.canvas.getContext('2d');
    this.body=document.querySelector('ion-content');
    this.canvas.height=Math.max(this.body.scrollHeight, this.body.offsetHeight);
    this.canvas.width=Math.max(this.body.scrollWidth, this.body.offsetWidth);
    
    let diffWidth = (this.platform.width()/this.baseWidth);
    let diffHeight = (this.platform.height()/this.baseHeight);
    this.sizeMultiplier=diffWidth;

    if(this.platform.width()>this.platform.height()){
      this.sizeMultiplier=diffHeight;
      this.dropSpeed=300;
    }
    
    this.playerSpeed = (2.5 * this.sizeMultiplier);
    this.spawnSpeedMax = (5 * diffHeight);
    
    let playerHeight = 25;
    let playerWidth = 25;

    this.player={
        x:0,
        y:(this.canvas.height - (this.canvas.height*.24)),
        h:playerHeight,
        w:playerWidth,
        s:this.playerSpeed,
        c:'white',
        d:'r'
    }
    
    this.spawns=[];
  }

  ionViewDidLoad() {
    this.init();
    this.launchSpawns();
    this.draw();
  }

  draw():void {

    this.setPlayerColor();

    this.moveSpawns();
    
    this.movePlayer();

    if(this.gamePlay==true){
      window.requestAnimationFrame(this.draw.bind(this));
    }
  }

  //Controls - change direction on keyup
  changeDirection(): void{
    if(this.player.d === 'l'){
      this.player.d = 'r';
    }else if(this.player.d === 'r'){
      this.player.d = 'l';
    }
    //Trigger a player redraw in the opposite direction
    //on click rather than on the next animation frame.
    this.movePlayer();
  }

  private movePlayer():void{
    this.ctx.save();

    this.ctx.fillStyle = this.player.c;

    this.ctx.clearRect(
      this.player.x-1, 
      this.player.y-1, 
      this.player.w+2, 
      this.player.h+2
    );

    if(this.player.x >= (this.canvas.width - this.player.w)){
      this.player.d='l';
    }

    if(this.player.x <= 0){
      this.player.d='r';
    }

    if(this.player.d === 'r'){
      this.player.x = (this.player.x + this.player.s);
    }else{
      this.player.x = (this.player.x - this.player.s);
    }

    this.ctx.fillRect(
      this.player.x,
      this.player.y, 
      this.player.w, 
      this.player.h
    );

    this.ctx.restore();
  }

  private launchSpawns():void{
    this.spawner = setInterval(()=>{
      //Use psuedo-random strings to name the new spawns
      var text = "";
      var possible = "abcdefghijklmnopqrstuvwxyz";

      for (var i = 0; i < 10; i++){
        text += possible.charAt(Math.floor(Math.random() * possible.length));
      }

      let type = 'posion';
      let size = 5;
      let pts = 10;
      let color = 'yellow';

      this.spawns[text] = {
          x:Math.floor(Math.random()*this.canvas.width),
          y:0,
          h:size,
          w:size,
          s:this.getRandomInt(this.spawnSpeedMin, this.spawnSpeedMax),
          t:type,
          c:color,
          p:pts,
          counted:false
      }

    },this.dropSpeed);
  }

  private moveSpawns():void{
    for(let spawn in this.spawns){

      if(this.spawns[spawn].y<=this.canvas.height){

        
        if(this.spawns[spawn].c === 'rand'){
          this.ctx.fillStyle = this.randomColor();this.randomPastel(64,64);
        }else{
          this.ctx.fillStyle = this.spawns[spawn].c;
        }

        this.ctx.save();

        this.ctx.clearRect(
          this.spawns[spawn].x-1, 
          this.spawns[spawn].y-1, 
          this.spawns[spawn].w+2, 
          this.spawns[spawn].h+2
        );

        this.ctx.fillRect(
          this.spawns[spawn].x, 
          this.spawns[spawn].y = (this.spawns[spawn].y+this.spawns[spawn].s), 
          this.spawns[spawn].w, 
          this.spawns[spawn].h
        );

        this.ctx.restore();

        //collision
        if(this.spawns[spawn].t == 'posion'){
          if (
              this.player.x < this.spawns[spawn].x + this.spawns[spawn].w &&
              this.spawns[spawn].x > this.player.x && this.spawns[spawn].x < (this.player.x + this.player.w) &&
              this.player.y < this.spawns[spawn].y + this.spawns[spawn].h &&
              this.player.y + this.player.h > this.spawns[spawn].y
          ){

            //cancelAnimationFrame(this.gamePlay);
            this.gamePlay=false;
  
            setInterval(()=>{
              this.canvas.style.background = this.randomColor();
            },100);

            setTimeout(()=>{
              this.navCtrl.push(GameOverPage, {score: this.score});
            }, 1500);

          }
        }

        if(this.spawns[spawn].t == 'bonus'){
          if (
            this.player.x < this.spawns[spawn].x + this.spawns[spawn].w &&
            this.spawns[spawn].x > this.player.x && this.spawns[spawn].x < (this.player.x + this.player.w) &&
            this.player.y < this.spawns[spawn].y + this.spawns[spawn].h &&
            this.player.y + this.player.h > this.spawns[spawn].y
          ){

            if(this.spawns[spawn].counted==false){
              this.setScore(this.spawns[spawn]);
              this.spawns[spawn].counted=true;
            }

            this.ctx.clearRect(
              this.spawns[spawn].x, 
              this.spawns[spawn].y, 
              this.spawns[spawn].w, 
              this.spawns[spawn].h
            );

            delete this.spawns[spawn];
            
          }
        }
      }else{
        if(this.spawns[spawn].counted==false && this.spawns[spawn].t == 'posion'){
          this.setScore(this.spawns[spawn]);
          this.spawns[spawn].counted=true;
          delete this.spawns[spawn];
        }
      }
    }
  }

  private getRandomInt(min:number, max:number):number {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  private randomColor(): string{
    return '#' + Math.floor(Math.random()*16777215).toString(16);
  }

  private randomPastel (colorNum, colors){
    if (colors < 1) colors = 1; // defaults to one color - avoid divide by zero
    return "hsl(" + (colorNum * (360 / colors) % 360) + ",100%,80%)";
  }

  private setScore(spawn:Spawn): void{
    let newPts = Math.round(spawn.p * this.scoreMultiplier);
    this.score=this.score+newPts;
  }

  private setPlayerColor(): void{
    if(this.score < 1000 ){
       this.player.c= '#ffffff';
    }else if(this.score > (this.lastThousand+1000)){
      this.scoreMultiplier = this.scoreMultiplier + .25;
      this.lastThousand = this.score;
      this.player.c = this.randomPastel(
        this.getRandomInt(1,32),
        32
      );
    }
  }
}



